body {
  background-color: #ccc;
  font-family: "Roboto";
  font-weight: normal;
}
#root {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 50px 0;
}
.main {
  width: 100%;
  max-width: 500px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 2px 2px 5px #aaa, -2px -2px 5px #aaa;
}
.main h1 {
  text-align: center;
  font-weight: normal;
  font-size: 27px;
}
.img-container {
  height: 80px;
  border-radius: 50%;
  border: 1px solid #ccc;
  flex-basis: 80px;
  margin: 0 10px 0 0;
  box-sizing: border-box;
}
.birthday {
  width: 100%;
  height: auto;
  border-top: 1px solid #ccc;
  border-width: 1px 0;
  padding: 10px;
  padding-left: 20px;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  position: relative;
}
.birthday:last-of-type {
  border-bottom: 1px solid #ccc;
}
.birthday-info h2 {
  margin: 0;
  font-size: 20px;
  margin-bottom: 10px;
  font-weight: normal;
}
.birthday-info span {
  color: rgb(87, 87, 87);
  font-weight: lighter;
}
.delete-button {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  cursor: pointer;
  background-color: white;
  border: 0;
}
.delete-button:hover,
.delete-button:focus {
  color: red;
}
.clear-all {
  width: calc(100% - 40px);
  margin: 25px 20px;
  padding: 10px 15px;
  text-align: center;
  background-color: rgb(125, 125, 255);
  border: 0;
  color: white;
  font-size: 18px;
  box-sizing: border-box;
}
footer {
  text-align: center;
}
footer i {
  color: red;
}
footer a {
  color: rgb(65, 65, 255);
}
@media (max-width: 450px) {
  #root {
    padding: 20px 0;
  }
}
